.text_size_controler {
  font-family: Rubik;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #ffffff;
  margin: 0px 2.5px;
  background-color: #afa39e;
  width: 9%;
  height: 10%;
  box-shadow: 1px 4px 10px rgba(0, 151, 136, 0.2);
  border-radius: 8px 8px 0px 0px;
  padding: 0px 2.5px;
}

.text_size_controler > span {
  padding: 2.5px;
}
