.start_read {
  border-radius: 8px;
  padding: 10px, 13px, 10px, 13px;
  background: rgba(0, 151, 136, 0.5);
  padding: 1%;
  margin-right: 10px;
  cursor: pointer;
}

.start_read > span {
  font-family: Rubik;
  font-size: 13pt;
  font-style: normal;
  font-weight: 800;
  line-height: 20px;
  letter-spacing: 0.025em;
  color: #ffffff;
}
