.delete_text {
  border-radius: 8px;
  padding: 10px, 13px, 10px, 13px;
  background: rgba(0, 151, 136, 0.5);
  padding: 1%;
  cursor: pointer;
}

.delete_text > span {
  font-family: Rubik;
  font-size: 12pt;
  font-style: normal;
  font-weight: 800;
  line-height: 20px;
  letter-spacing: 0.025em;
  color: #ffffff;
}
